.container {
  display: flex;
  padding: 3rem;
  min-width: 12rem;
}

.card {
  width: 10rem;
  height: 15rem;
  border-radius: 1rem;
  transition: all 0.3s ease-in-out;
}

.card:hover {
  box-shadow: 10px 10px 28px 1px rgba(0, 0, 0, 0.43);
  -webkit-box-shadow: 10px 10px 28px 1px rgba(0, 0, 0, 0.43);
  -moz-box-shadow: 10px 10px 28px 1px rgba(0, 0, 0, 0.43);
  transform: rotate(360deg);
  /* transform: scale(1.1, 1.1); */
  color: red;
  cursor: pointer;
  scale: 2;
}

.card img {
  border-radius: 1rem;
  width: 10rem;
  height: 10rem;
}

.footer {
  display: flex;
  flex-direction: column;
}

.footer h3,
p {
  margin: 0px;
  padding: 0.5rem;
}
